a {
	color: rgb(56, 58, 59);
	text-decoration: none;
	font-family: 'Open Sans Condensed', sans-serif;
	font-weight: normal;
	letter-spacing: 0.03em;

	&:hover {
		color: #D424B1;
	}
}

/* paragraphs */

p {
	line-height: 1.5;
	font-family: 'Roboto', sans-serif;
	letter-spacing: 0.03em;
}

/*blockquotes*/

blockquote {
	background: #F7F7F7;
	margin: 0;
	padding: 20px;
	p {
		margin-top: 0;
	}
}

/*tables*/

table {
	td {
		padding: 10px;
	}
	tr td:first {
		padding-right: 0;
	}
}

/*def lists*/

dt {
	font-weight: 600;
	padding-bottom: 5px;
}

dd {
	padding: 10px;
	margin-left: 10px;
}

/*lists*/

ul li, ol li {
	padding: 3px;
}

/*PRE*/

pre {
	background: #F7F7F7;
	padding: 10px;
	border-radius: 4px;
	border: 1px solid #ccc;
}


/*
====================================
Global Classes / Wordpress Specific
====================================
*/

.aligncenter {
	text-align: center;
	margin: 0 auto;
}

.alignleft {
	float: left;
}

.alignright {
	float: right;
}

img {
	&.alignleft {
		padding-right: 5px;
	}
	&.alignright {
		padding-left: 5px;
	}
}

.clear {
	width: 100%;
	float: left;
	clear: both;
}

h1, h2, h3, h4 {
	margin:0;
	padding:0;
	font-family: 'Roboto', sans-serif;
	text-transform: uppercase;
	letter-spacing: 0.05em;

}

.aboutTitle h2 {
	display: inline-block;
	letter-spacing: 5px;
	font-family: 'Open Sans Condensed', sans-serif;

}



.temporaryHeader h2 {
	display: inline-block;
	letter-spacing: 5px;
	font-family: 'Open Sans Condensed', sans-serif;
}



html {
	
	background-image: url("../images/linen/linen.png");
}